    route_state_arr:[]
  }},
  mounted(){
    
  },
  methods:{
    
    re_init(){
      this.route_state_arr.splice(0,2)
      this.$router.push('/')
    },
    onchangenav(x){
      this.route_state_arr.splice(0,2,x.ptit,x.title)
      this.$router.push({
        path:`/${x.index}`,
        query:{}
      })
    },
    logout(){
      localStorage.clear()
      this.$router.push('/login')
    }
  },
