
<style scoped lang='scss'>
  .login {
    // 页面容器
    &-container {
      background-color: #2b4b6b;
      height: 100%;
      overflow: hidden;
    }
    // 登录面板容器
    &-box {
          width: 450px;
    height: 304px;
    background-color: #fff;
    border-radius: 4px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
      // 头像框
      .avatar-box {
        width: 130px;
    height: 130px;
    border: 1px solid #eee;
    border-radius: 50%;
    padding: 8px;
    box-shadow: 0 0 10px #eee;
    position: absolute;
    background-color: #fff;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    img {
          width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: #eee;
    }
      }
    }
    // 表单区
    &-form {
      position: absolute;
      bottom: 0;
      width: 100%;
      padding: 20px;
      box-sizing: border-box;
      &>div.el-form-item {
        &>div.el-form-item__content {
          &>div.el-input--prefix {
            position: relative;
            font-size: 14px;
            display: inline-block;
            width: 100%;
            input {
              border-color: #67c23a;
                -webkit-appearance: none;
                background-color: #fff;
                background-image: none;
                border-radius: 4px;
                border: 1px solid #dcdfe6;
                -webkit-box-sizing: border-box;
                box-sizing: border-box;
                color: #606266;
                display: inline-block;
                font-size: inherit;
                height: 40px;
                line-height: 40px;
                outline: 0;
                padding: 0 15px;
                -webkit-transition: border-color .2s cubic-bezier(.645,.045,.355,1);
                transition: border-color .2s cubic-bezier(.645,.045,.355,1);
                width: 100%;
            }
            span {
              left: 5px;
              transition: all .3s;
              position: absolute;
              top: 0;
              -webkit-transition: all .3s;
              height: 100%;
              color: #c0c4cc;
              text-align: center;
              i {
                    font-family: iconfont!important;
                font-size: 16px;
                font-style: normal;
                -webkit-font-smoothing: antialiased;
              }
            }
          }
          button {

          }
        }
      }
    
    }
  }
</style>
<template>
  <div class="login-container">
      <div class="login-box">
        <div class="avatar-box">
          <img
            :src="unknown_src"
            alt
          />
        </div>
        <el-form ref="form" class="login-form" v-model="login" label-width="80px" size="mini">
          <el-form-item class="userbox cf">
            <el-input placeholder='请输入用户名' v-model="login.name" prefix-icon='el-icon-user'></el-input>
            
          </el-form-item>
          <el-form-item class="snbox cf">
            <el-input placeholder="请输入密码" type="password" prefix-icon='el-icon-menu' v-model="login.sn" autocomplete="off"></el-input>

          </el-form-item>
          <el-form-item class="logbox cf">
            <el-button type="primary" @click="sign_in">登录</el-button>
          </el-form-item>
        </el-form>
    </div>
  </div>
</template>


<script>
export default {
  data() {
    return {
      login:{
        name: "admin",
        sn: "123456"
      },
      unknown_src:'data:image/png;base64,'
    };
  },
  created() {},
  mounted() {
    this.onmouted()
  },
  //props:{},//[]
  methods: {
    async onmouted(){
      
    },
    async sign_in() {
      let {data: res} = await this.$http.post(
        `/login`, {
          username: this.login.name,
          password: this.login.sn
        }
      );
      console.log(res);
      if(res.meta.status==200){
        let {data:{token}}=res
        localStorage.token=token
        localStorage.username=res.data.username
        
        if(this.$route.query.raw_url){
          this.$router.push(this.$route.query.raw_url);
        }else{
          this.$router.push("/");
        }
      }
    }
  },
  computed: {},
  watch: {},
  filters: {},
  directives: {},
  components: {}
};
</script>