import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import login from '../views/login'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    beforeEnter(to,from,next){
      if(!localStorage.token){
        next('/login')
      }
      next()
    },
    children:[
      {
        path: '/default',
        name: 'default',
        alias:'/',
        component: ()=>import ('@/views/subviews/default')
      },
      {
        path: '/productlist',
        name: 'productlist',
        component: ()=>import ('@/views/subviews/productlist')
      },
      {
        path: '/datareport',
        name: 'datareport',
        component: ()=>import ('@/views/subviews/datareport')
      },
      {
        path: '/orderlist',
        name: 'orderlist',
        component: ()=>import ('@/views/subviews/orderlist')
      },
      {
        path: '/powerlist',
        name: 'powerlist',
        component: ()=>import ('@/views/subviews/powerlist')
      },
      {
        path: '/productsort',
        name: 'productsort',
        component: ()=>import ('@/views/subviews/productsort')
      },
      {
        path: '/rolelist',
        name: 'rolelist',
        component: ()=>import ('@/views/subviews/rolelist')
      },
      {
        path: '/sortarg',
        name: 'sortarg',
        component: ()=>import ('@/views/subviews/sortarg')
      },
      {
        path: '/userlist',
        name: 'userlist',
        component: ()=>import ('@/views/subviews/userlist')
      }
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: login
  },
  
]

const router = new VueRouter({
  routes
})

export default router
